.profile-section {
  padding: 30px 0;
  .user {
    display: flex;
    align-items: center;
    i {
      font-size: 35px;
      color: #f3a01e;
      margin-bottom: 10px;
    }
    .info {
      padding: 0 15px;
      h3 {
        font-size: 20px;
        font-family: "Roboto-Regular";
        text-transform: capitalize;
        margin-bottom: 0;
      }
      p {
        color: #919294;
        text-transform: capitalize;
        font-family: "Roboto-Regular";
        font-size: 16px;
        margin-bottom: 0;
        @media (max-width: 1024px) {
          font-size: 15px;
        }
      }
    }
  }
  .selectPage {
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    overflow: hidden;
    margin-top: 20px;
    @media (max-width: 768px) {
      margin-bottom: 27px;
    }
    .selectBox {
      padding: 15px;
      display: flex;
      width: 100%;
      background-color: #fff;
      border: none;
      align-items: center;
      transition: 0.5s;
      &:not(:last-of-type) {
        border-bottom: 1px solid #919294;
      }
      i {
        font-size: 27px;
        margin-right: 10px;
        color: #f3a01e;
        transition: 0.5s;

        body.ar & {
          margin-right: unset;
          margin-left: 10px;
        }
      }
      h4 {
        margin-bottom: 0;
        color: #919294;
        transition: 0.5s;
        text-transform: capitalize;
        font-family: "Roboto-Regular";
        font-size: 20px;
        @media (max-width: 1024px) {
          font-size: 17px;
        }
      }
      &.mixitup-control-active {
        background-color: #f3a01e;
        &:not(:last-of-type) {
          border-bottom: 1px solid #f3a01e;
        }
        i,
        h4 {
          color: #000;
        }
      }
    }
  }
  .sections {
    .content {
      .section-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        button {
          background-color: #f3a01e;
          border-radius: 10px;
          border: none;
          padding: 5px 20px;
          font-family: "Roboto-Regular";
          font-size: 25px;
          text-transform: capitalize;
          @media (max-width: 480px) {
            font-size: 15px;
          }

          transition: 0.5s;
          &:hover {
            background-color: #e91515;
            color: #fff;
          }
        }
        .section-title {
          text-transform: capitalize;
          font-size: 25px;
          font-family: "Roboto-Regular";
          margin-bottom: 0;
          @media (max-width: 480px) {
            font-size: 18px;
          }
        }
      }
      .profile-container {
        padding: 27px 0;
        .order-box {
          display: flex;
          @media (max-width: 480px) {
            flex-direction: column;
            text-align: center;
            justify-content: center;
            align-items: center;
          }

          align-items: center;
          background-color: #f9f9f9;
          border-radius: 10px;
          padding: 10px;
          width: 90%;
          margin-bottom: 20px;
          @media (max-width: 768px) {
            width: 100%;
          }

          .image-box {
            padding: 15px;
          }
          .info {
            h3 {
              font-size: 30px;
              font-family: "Roboto-Regular";
            }
            h4 {
              color: #4399d4;
              text-transform: uppercase;
              font-size: 25px;
              font-family: "Roboto-Bold";
            }
            p {
              font-size: 16px;
              font-family: "Roboto-Regular";
              letter-spacing: 1.6px;
              width: 90%;
              @media (max-width: 480px) {
                width: 100%;
              }
            }
            h6 {
              font-size: 16px;
              font-family: "Roboto-Medium";

              span {
                margin-left: 10px;
              }
            }
          }
        }
      }
      .wish-container {
        padding: 27px 0;
        .order-box {
          display: flex;
          @media (max-width: 480px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
          }

          align-items: center;
          background-color: #f9f9f9;
          border-radius: 10px;
          padding: 10px;
          width: 90%;
          @media (max-width: 768px) {
            width: 100%;
          }

          margin-bottom: 20px;
          .image-box {
            padding: 15px;
            width: 25%;
            @media (max-width: 480px) {
              width: 100%;
            }
          }
          position: relative;
          button.close {
            position: absolute;
            top: 11px;
            right: 11px;
            color: #e91515;
            transition: 0.5s;
          }
          .info {
            width: 100%;
            h3 {
              font-size: 30px;
              font-family: "Roboto-Regular";
            }
            h4 {
              color: #4399d4;
              text-transform: uppercase;
              font-size: 25px;
              font-family: "Roboto-Bold";
            }

            button.open-side-cart {
              display: block;
              margin-left: auto;
              body.ar & {
                margin-left: unset;
                margin-right: auto;
              }
              @media (max-width: 480px) {
                margin-left: unset;
                margin-right: unset;
                margin: auto;
              }
              background-color: #f3a01e;
              border-radius: 10px;
              border: none;
              padding: 5px 20px;
              font-family: "Roboto-Regular";
              text-transform: capitalize;
              font-size: 25px;
              transition: 0.5s;
              &:hover {
                background-color: #4399d4;
                color: #fff;
              }
            }
          }
        }
      }
      .userDocument {
        padding: 40px 0;
        .userDocument-box {
          text-align: center;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          .image-box {
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            box-shadow: 0px 3px 6px #00000029;
            margin-bottom: 20px;
            margin-top: 20px;
            border-radius: unset;
            height: 250px;
          }
          h2 {
            text-transform: capitalize;
            font-size: 20px;
            font-family: "Roboto-Regular";
            margin-bottom: 30px;
            color: $dark;
          }
        }
      }

      .accountSettings {
        border: 1px solid #707070;
        border-radius: 10px;
        padding: 15px;
        h3 {
          font-size: 20px;
          font-family: "Roboto-Medium";
        }
        p {
          letter-spacing: 0px;
          color: #919294;
          font-family: "Roboto-Regular";
          font-size: 16px;
          max-width: 85%;
          @media (max-width: 480px) {
            max-width: 100%;
          }
        }
        a {
          background-color: #919294;
          color: #fff;
          display: block;
          width: 100px;
          text-align: center;
          border-radius: 10px;
          border: none;
          padding: 3px 20px;
          font-family: "Roboto-Regular";
          font-size: 25px;
          text-transform: capitalize;
          margin-left: auto;
          display: block;
          @media (max-width: 480px) {
            font-size: 17px;
          }

          body.ar & {
            margin-left: unset;
            margin-right: auto;
          }
          transition: 0.5s;
          &:hover {
            background-color: #f3a01e;
          }
        }
      }
    }
  }
}
