.min-header {
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-family: "Roboto-Regular";
  font-size: 40px;
}
.product-conatiner {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
  @media (max-width: 1024px) {
    justify-content: center;
    align-items: center;
  }
  &.filter {
    justify-content: center;
  }
  .product-box {
    width: calc(100% / 4.312);
    margin: 10px;
    background-color: #fff;
    box-shadow: 0px 0px 15px rgba(#000, 0.2);
    border-radius: 40px;
    margin-bottom: 30px;
    @media (max-width: 1024px) {
      width: calc(100% / 3.3);
      justify-content: center;
      align-items: center;
    }
    @media (max-width: 991px) {
      width: calc(100% / 2.2);
      margin-bottom: 30px;
    }
    @media (max-width: 480px) {
      width: 100%;
      margin: 10px 0;
      margin-bottom: 37px;
    }

    .image-container {
      height: 250px;
      // overflow: hidden;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      justify-content: center;
      img {
        padding-top: 26px;
        height: 70%;
        display: block;
      }
      .options {
        position: absolute;
        display: flex;
        justify-content: space-between;
        width: 100%;
        .favorite {
          position: absolute;
          left: 10px;
          top: -98px;
          body.ar & {
            right: 10px;
            left: unset;
          }

          margin-top: 7px;
          width: 42px;
          height: 42px;
          background-color: $color-main;
          border-radius: 100px;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 0.5s;
          i {
            color: #fff;
            font-size: 25px;
          }
          &:hover {
            background-color: $color-dark;
          }
        }
        .sale {
          width: 90%;
          position: absolute;
          border-radius: 40px;
          letter-spacing: 2px;
          font-size: 20px;
          height: 40px;
          background-color: $red;
          display: flex;
          justify-content: center;
          color: #fff;
          align-items: center;
          font-family: "staravenue";
          // align-self: center;
          left: 50%;
          transform: translateX(-50%);
          top: -139px;
          span {
            margin-top: 5px;
            span {
              font-family: "Roboto-Medium";
            }
          }
        }
      }
    }
    .content {
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      h2 {
        font-size: 20px;
        font-family: "Roboto-Regular";
        width: 80%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      p{
        color: #707070;
        font-size: 12px;
        width: 80%;
        @media (max-width:480px){
          width: 100%;
        }
      }
      .stars-box {
        i {
          font-size: 17px;
          color: #919296;
          &.gold {
            color: $color-main;
          }
          &:not(:last-of-type) {
            // margin-right: 10px;
            body.ar & {
              margin-right: unset;
              // margin-left: 10px;
            }
          }
        }
      }
      .price-box {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
        flex-direction: column;
        h3 {
          font-size: 20px;
          margin: 0;
          &.price-new {
            color: $color-dark;
            margin-bottom: 10px;
          }
          &.price-old {
            margin: 0 10px;
            font-family: "Roboto-Regular";
            color: #a4b3b6;
            text-decoration: line-through;
          }
        }
      }
      a {
        display: flex;
        text-align: center;
        margin: 10px 0;
        color: #fff;
        border-radius: 100px;
        display: flex;
        padding: 7px 10px;
        width: 88%;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-family: "Roboto-Regular";
        background-image: linear-gradient(
          0deg,
          $color-dark,
          $color-dark,
          $color-main,
          $color-main,
        );
        background-size: 100% 300%;
        transition: all 0.5s ease-in-out;
        i {
          fill: #fff;
          transition: 0.5s;
          font-size: 20px;
          margin-left: 7px;
          body.ar & {
            margin-left: unset;
            margin-right: 7px;
          }
        }
        &:hover {
          background-position-y: 100%;
          color: #fff;
        }
      }
    }
    .cart {
      margin-top: 10px;
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      display: flex;
      color: #fff;
      transition: 0.5s;
      border: unset;
      width: 100%;
      border-radius: 100px;
      span {
        display: block;
        font-family: "Roboto-Regular";
        font-size: 20px;
        transition: 0.5s;
      }
      svg {
        fill: #fff;
        width: 20px;
        margin-left: 7px;
        body.ar & {
          margin-left: unset;
          margin-right: 7px;
        }

        transition: 0.5s;
      }
      background-image: linear-gradient(
        0deg,
        $color-main,
        $color-main,
        $color-dark,
        $color-dark
      );
      background-size: 100% 300%;
      transition: all 0.5s ease-in-out;

      &:hover {
        background-position-y: 100%;
        span {
          color: $dark;
        }
        svg {
          fill: $dark;
        }
      }
    }
  }
  &.filter .product-box {
    width: calc(100% / 3.312);
    @media (max-width: 1024px) {
      width: calc(100% / 2.2);
      justify-content: center;
      align-items: center;
    }
    @media (max-width: 991px) {
      width: calc(100% / 2.2);
      margin-bottom: 40px;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }
}
.head-title {
  font-size: 40px;
  font-family: "Roboto-Regular";
  text-transform: uppercase;
  margin-bottom: 30px;
  letter-spacing: 5px;
  @media (max-width: 991px) {
    text-align: center;
    font-size: 30px;
  }
}
.sup-title {
  letter-spacing: 2px;
  color: #707070;
  font-size: 20px;
  font-family: "Roboto-Medium";
  margin-bottom: 30px;
  @media (max-width: 991px) {
    text-align: center;
    margin-top: -10px;

    font-size: 17px;
  }
}
.product-box2 {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  border-radius: 40px;
  .image-box {
    height: 350px;
    padding: 10px;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(#000, 0.2);
    background-color: #fff;
    width: 100%;
    img {
      height: 80%;
      display: block;
    }
  }
  h2 {
    font-size: 18px;
    overflow: hidden;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .stars-box {
    i {
      font-size: 17px;
      color: #919296;
      &.gold {
        color: $color-main;
      }
      &:not(:last-of-type) {
        // margin-right: 10px;
        body.ar & {
          margin-right: unset;
          // margin-left: 10px;
        }
      }
    }
  }
  .price-box {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    flex-direction: column;
    h3 {
      font-size: 20px;
      margin: 0;
      &.price-new {
        color: $color-dark;
      }
      &.price-old {
        margin: 0 10px;
        font-family: "Roboto-Regular";
        color: #a4b3b6;
        text-decoration: line-through;
      }
    }
    .min-button {
      margin-top: 10px;
      font-size: 13px;
    }
  }
}
.overlayCart {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(#000, 0.6);
  z-index: 100;
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
  &.active {
    opacity: 1;
    visibility: visible;
  }
}

.left-cart-container {
  background-color: #fff;
  width: 300px;
  height: 100%;
  position: fixed;
  right: 0;
  z-index: 200;
  .header {
    padding: 10px;
    display: flex;
    background-color: $color-dark2;
    align-items: center;
    color: #f7f7f7;
    justify-content: space-between;
    h2 {
      margin-bottom: 0;
      font-size: 20px;
    }
    i {
      color: #f7f7f7;
      margin-bottom: 5px;
      font-size: 17px;
      transition: 0.5s;
    }
  }
  .content {
    height: 100vh;
    overflow-y: scroll;
    padding: 30px 15px 40px;
  }
  .product-box {
    display: flex;
    position: relative;
    margin-bottom: 10px;
    .close {
      position: absolute;
      right: -10px;
      top: 0px;
      color: #e91515;
      font-size: 17px;
      transition: 0.5s;
      body.ar & {
        right: unset;
        left: -13px;
      }
    }
    .image-box {
      img {
        display: block;
        width: 62px;
      }
    }
    .description {
      padding: 0 10px;
      .price {
        color: #e91515;
      }
      .control {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        p {
          margin-bottom: 0;
          margin-right: 10px;
          body.ar & {
            margin-right: unset;
            margin-left: 10px;
          }
        }
      }
      h3 {
        font-size: 15px;
      }
    }
  }
  .min-button:last-of-type {
    margin-top: 10px;
  }
  .controls {
    .box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      h4 {
        margin-bottom: 0;
        font-size: 20px;
      }
      p {
        margin-bottom: 0;
      }
    }
  }
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  transition: 0.5s;
  &.active {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
}
.counter {
  display: flex;
  align-items: center;
  button {
    background-color: unset;
    border: unset;
    outline: unset;
    color: $color-dark;
  }
  span {
    min-width: 10%;
    text-align: center;
    display: block;
    font-size: 20px;
  }
}
.sections-title {
  font-family: "Roboto-Regular";
  font-size: 40px;
  text-transform: capitalize;
  @media (max-width: 991px){
    font-size: 30px;
    text-align: center;
  }
}
.form-section {
  padding: 40px 0;
  form {
    .form-title {
      margin-bottom: 30px;
      @media (max-width: 991px) {
        margin-bottom: 10px;
      }

      h2 {
        text-transform: uppercase;
        letter-spacing: 4px;
        font-family: "Roboto-Regular";
        font-size: 40px;
        margin-bottom: 50px;
        @media (max-width: 991px) {
          font-size: 30px;
          margin-bottom: 30px;
        }
      }
      p {
        letter-spacing: 2px;
        color: #919294;
        font-family: "Roboto-Medium";
        font-size: 20px;
        @media (max-width: 991px) {
          font-size: 17px;
        }
      }
    }
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    @media (max-width: 768px) {
      width: 100%;
    }
    .input-container {
      width: 100%;
      margin-bottom: 20px;
      input {
        width: 100%;
        border-radius: 10px;
        border: 1px solid #919294;
        height: 47px;
        padding: 10px;
        letter-spacing: 1.6px;
        color: #707070;
        text-transform: capitalize;
        font-family: "Roboto-Regular";
        outline: none;
        transition: 0.4s;
      }
      textarea {
        width: 100%;
        border-radius: 10px;
        border: 1px solid #919294;
        outline: none;
        padding: 10px;
        letter-spacing: 1.6px;
        color: #707070;
        text-transform: capitalize;
        font-family: "Roboto-Regular";
        min-height: 162px;
        transition: 0.4s;
      }
      input:focus,
      textarea:focus {
        border: 1px solid $color-main;
      }
    }
  }
}
.editProfile-section {
  padding: 30px 0;
  .form-container {
    width: 30%;
    @media (max-width: 768px) {
      width: 60%;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
      text-transform: capitalize;
      font-family: "Roboto-Medium";
      text-align: center;
      font-size: 20px;
      margin-bottom: 30px;
    }
    form {
      width: 100%;
    }
    .input-box {
      display: flex;
      flex-direction: column;
      margin-top: 10px;
      width: 100%;

      label {
        font-size: 20px;
        font-family: "Roboto-Regular";
      }
      input {
        width: 100%;
        display: block;
        padding: 10px;
        border: 1px solid #cdcdcd;
        border-radius: 10px;
        color: #cdcdcd;
      }
    }
    .min-button {
      margin: 40px auto 0;
    }
  }
}
