.min-button {
  border: none;
  outline: none;
  color: $dark;
  border-radius: 100px;
  display: flex;
  padding: 5px 15px;
  width: 140px;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-family: "Roboto-Regular";
  font-size: 20px;
  background-image: linear-gradient(
    0deg,
    $color-dark2,
    $color-dark2,
    $color-main,
    $color-main
  );
  &.reverse {
    background-image: linear-gradient(
      0deg,
      $color-main,
      $color-main,
      $color-dark2,
      $color-dark2
    );
  }
  &.icon {
    font-size: 13px;
  }
  background-size: 100% 300%;
  transition: all 0.5s ease-in-out;
  &.tow {
    margin: 20px auto 0;
    background-image: linear-gradient(
      0deg,
      $color-dark,
      $color-dark,
      $color-main,
      $color-main
    );
  }
  &.orange {
    background-image: linear-gradient(
      0deg,
      $color-dark,
      $color-dark,
      $color-main,
      $color-main
    );
  }

  &.three {
    margin: 20px auto 0;
    background-image: linear-gradient(
      0deg,
      $color-main,
      $color-main,
      #ffffff,
      #ffffff
    );
    border: 2px solid $color-main;
    color: $dark;
  }
  svg {
    fill: $dark;
    transition: 0.5s;
    width: 17px;
    margin-left: 7px;
    body.ar & {
      margin-left: unset;
      margin-right: 7px;
    }
  }
  &:hover {
    background-position-y: 100%;
  }
}
.d-flex {
  .min-button {
    &.tow {
      margin: unset;
      margin-top: 20px;
      margin-left: auto;
      body.ar & {
        margin-left: unset;
        margin-right: auto;
      }
    }
    &.three {
      margin: unset;
      margin-top: 20px;
      margin-right: auto;
      body.ar & {
        margin-right: unset;
        margin-left: auto;
      }
    }
  }
}
