.newVilla-section {
  padding: 20px 0;
}
.camera-solution-section {
  padding: 20px 0;
  .content {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    h3 {
      text-align: center;
      letter-spacing: 2px;
      font-family: "Roboto-Medium";
      font-size: 20px;
      margin-bottom: 70px;
      margin-top: 70px;
      @media (max-width: 425px) {
        margin-bottom: 40px;
        margin-top: 40px;
      }
    }

    .solution-map {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        background-color: #707070;
        top: 50%;
        transform: translateY(-50%);
        height: 1px;
      }
      .solution-content {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 80px;
        width: max-content;
        padding: 5px;
        justify-content: flex-end;
        position: relative;
        background-color: #fff;
        svg {
          margin-bottom: 10px;
          @media (max-width: 425px) {
            width: 31px;
          }
          g path.a {
            fill: #707070;
          }
        }
        h4 {
          color: #707070;
          font-size: 16px;
          text-align: center;
          @media (max-width: 425px) {
            font-size: 13px;
          }
          @media (max-width: 320px) {
            font-size: 10px;
          }
        }
        &.active {
          svg g path.a {
            fill: $color-main;
          }
          h4 {
            color: $color-main;
          }
        }
        &.old {
          svg g path.a {
            fill: $color-dark;
          }
          h4 {
            color: $color-dark;
          }
        }
      }
    }
    .location-type {
      .title {
        margin-bottom: 40px;
        @media (max-width: 425px) {
          display: none;
        }
        h4 {
          margin-top: 20px;
          font-family: "Roboto-Medium";
          text-transform: uppercase;
        }
        p {
          letter-spacing: 1.6px;
          color: #707070;
          margin-top: 16px;
          width: 50%;
        }
      }
    }
    .title-bottom {
      h4 {
        margin-top: 30px;
        font-family: "Roboto-Medium";
        text-transform: uppercase;
      }
      p {
        color: #707070;
        margin-top: 16px;
        width: 50%;
      }
    }
    .image-container {
      box-shadow: 0px 3px 6px #00000029;
      margin-bottom: 16px;
      border-radius: 40px;
      img {
        width: 100%;
        border-radius: 40px;
      }
    }
    .description {
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-bottom: 20px;
      position: relative;
      h4 {
        margin-bottom: 0;
      }
      label {
        margin-bottom: 0;
        margin-left: 20px;
        cursor: pointer;
        body.ar & {
          margin-left: unset;
          margin-right: 20px;
        }
        position: relative;
        margin-top: 3px;
        color: $color-dark;
        i {
          font-size: 17px;
          transition: 0.3s;
          opacity: 1;
          backface-visibility: hidden;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        i.fa-check-square {
          opacity: 0;
          transition: 0.5s;
        }
      }
      input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        &:checked ~ label i {
          opacity: 0;
        }
        &:checked ~ label i.fa-check-square {
          opacity: 1;
        }
      }
    }
  }
}

.products-container {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  .product {
    display: flex;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    padding: 20px 0;
    align-items: center;
    &:not(:last-of-type) {
      border-bottom: 1px solid #d6d6d6;
    }
  }
  .image-box {
    width: calc(100% / 5);
    padding: 20px;
    box-shadow: 0px 3px 6px #00000029;
    margin-right: 10px;
    background-color: #fff;
    border-radius: 40px;
    @media (max-width: 767px) {
      width: calc(100% / 3);
    }
  }
  .content {
    display: flex;
    padding: 0 10px;
    @media (max-width: 767px) {
      flex-direction: column;
      text-align: center;
    }

    h2 {
      font-size: 20px;
      font-family: "Roboto-Medium";
      body.ar & {
        text-align: right;
      }
    }
    .stars-box {
      body.ar & {
        text-align: right;
      }
      i {
        font-size: 17px;
        color: #919296;
        &.gold {
          color: $color-main;
        }
        &:not(:last-of-type) {
          body.ar & {
            margin-right: unset;
          }
        }
      }
    }
    p {
      margin-top: 14px;
      letter-spacing: 1.6px;
      font-size: 16px;
      width: 80%;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
    .right {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .price {
        color: $color-dark;
      }
    }
  }
}
.installing-cameras {
  text-align: center;
  padding: 40px 0;
  h2 {
    letter-spacing: 1.5px;
    font-size: 30px;
    font-family: "Roboto-Medium";
  }
  p {
    color: #707070;
    font-size: 20px;
    font-family: "Roboto-Regular";
    margin-bottom: 40px;
  }
  .box {
    display: flex;
    width: 40%;
    margin: 0 auto;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  ul {
    padding: 10px;
    background-color: #fff;
    box-shadow: 0px 3px 6px #00000029;
    width: max-content;
    margin: auto;
    li {
      padding-bottom: 10px;
      display: flex;
      padding-bottom: 5px;
      padding-top: 10px;
      &:not(:last-of-type) {
        border-bottom: 1px solid #d6d6d6;
      }
      a {
        background-color: unset;
        border: unset;
        transition: 0.5s;
        color: #000;
        &:hover {
          color: $color-main;
        }
      }
    }
  }
  .cost {
    margin-top: 25px;
    font-size: 20px;
    letter-spacing: 2px;
    font-family: "Roboto-Medium";
  }
  .price {
    color: $color-dark;
    font-size: 28px;
    width: max-content;
    margin: 0 auto;
    font-family: "Roboto-Bold";
  }
}
