.about-section {
  padding: 30px 0;
  h2.min-title {
    text-align: center;
    text-transform: capitalize;
    font-family: "Roboto-Regular";
    font-size: 30px;
    margin: 20px 0;
  }
  @media (max-width: 768px) {
    .image-box {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 20px;
      margin-top: 20px;
    }
  }
  .top-header {
    padding: 15px 0;
    .parg {
      p {
        letter-spacing: 1px;
        font-size: 20px;
        margin-bottom: 0;
        &:first-of-type {
          margin-bottom: 6px;
        }
        @media (max-width: 768px) {
          text-align: center;
        }
      }
    }
    .image-box {
      box-shadow: 0px 3px 6px #00000029;
    }
  }
  .ourProject {
    padding: 20px 0;

    .slider {
      padding: 20px 0;
      .owl-carousel {
        .slide {
          box-shadow: 0px 3px 6px #00000029;
          margin-top: 10px;
          margin: 10px;
          .image-box {
            margin-bottom: 10px;
          }
          .content {
            padding: 10px;
            h3 {
              font-size: 20px;
              font-family: "Roboto-Medium";
            }
            p {
              letter-spacing: 0.7px;
              color: #919294;
              text-transform: capitalize;
              font-size: 14px;
              font-family: "Roboto-Regular";
              margin-bottom: 8px;
            }
            a {
              color: #f3a01e;
              text-transform: capitalize;
              letter-spacing: 0.7px;
              font-size: 14px;
              font-family: "Roboto-Regular";
              transition: 0.5s;
              &:hover {
                color: #4399d4;
              }
            }
          }
        }
        .owl-nav {
          display: flex;
          justify-content: space-between;
          width: 100%;
          position: absolute;
          top: 46%;
          transform: translateY(-50%);
          button {
            position: absolute;
            i {
              transition: 0.5s;
              font-size: 26px;
              color: #919294;
            }
            &.owl-prev {
              right: -3%;
              @media (max-width: 480px) {
                right: 0;
              }
            }
            &.owl-next {
              left: -3%;
              @media (max-width: 480px) {
                left: 0;
              }
            }
            &:hover i {
              color: #000;
            }
          }
        }
      }
    }
  }
  .ourClients {
    padding: 20px 0;
    .owl-carousel {
      .owl-stage {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .slide {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .owl-nav {
        display: flex;

        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 46%;
        transform: translateY(-50%);
        button {
          position: absolute;
          i {
            transition: 0.5s;
            font-size: 26px;
            color: #919294;
          }
          &.owl-prev {
            right: -3%;
            @media (max-width: 480px) {
              right: 0;
            }
          }
          &.owl-next {
            left: -3%;
            @media (max-width: 480px) {
              left: 0;
            }
          }
          &:hover i {
            color: #000;
          }
        }
      }
    }
  }
  .ourEmployees {
    .prag-title {
      width: 70%;
      @media (max-width: 768px) {
        width: 100%;
      }

      text-align: center;
      font-family: "Roboto-Regular";
      font-size: 16px;
      letter-spacing: 0px;
      color: #707070;
      margin: auto;
      margin-bottom: 30px;
    }
    .row {
      .col-lg-6 {
        margin-top: 30px;
        @media (max-width: 768px) {
          margin-top: 0px;
        }
      }

      p {
        letter-spacing: 0.4px;
        font-family: "Roboto-Regular";
        font-size: 20px;
        @media (max-width: 768px) {
          margin-bottom: 0px;
          text-align: center;
        }
      }
    }
    .image-box {
      position: relative;
      height: max-content;
      box-shadow: 0px 3px 6px #00000029;

      .overlay {
        position: absolute;
        width: 100%;
        background-color: #000000b8;
        bottom: 0px;
        margin-bottom: 0;
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        padding: 14px 0 5px;
        h3 {
          color: #ffffff;
          font-family: "Roboto-Regular";
          font-size: 20px;
        }
      }
    }
  }
}
