.productPage {
  padding: 30px 0;
  .head-title {
    margin-bottom: 70px;
    font-size: 30px;
    letter-spacing: 0;
    @media (max-width: 480px) {
      margin-bottom: 40px;
    }
  }
  .owl-carousel {
    box-shadow: 0px 3px 6px #00000029;
    .image-box {
      padding: 17px;
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .hashs {
    display: flex;
    margin-top: 14px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }

    a {
      box-shadow: 0px 3px 6px #00000029;
      border: 1px solid #919294;
      display: block;
      padding: 10px;
      margin-right: 5px;
      width: 28%;
      transition: 0.5s;
      @media (max-width: 767px) {
        width: 27%;
      }

      &:hover {
        border-color: $color-main;
      }
    }
  }

  .content {
    @media (max-width: 375px) {
      text-align: center;
    }

    h2 {
      font-size: 30px;
      font-family: "Roboto-Regular";
      margin-bottom: 15px;
      @media (max-width: 480px) {
        font-size: 27px;
      }
    }
    .stars-box {
      margin-bottom: 15px;
      i {
        font-size: 17px;
        color: #919296;
        &.gold {
          color: $color-main;
        }
      }
    }

    p {
      letter-spacing: 1.6px;
      font-family: "Roboto-Regular";
      font-size: 16px;
      width: 90%;
      margin-bottom: 0;
      @media (max-width: 375px) {
        width: 100%;
      }
    }
    .price-box {
      display: flex;
      align-items: center;
      text-align: center;
      padding-top: 10px;
      padding-bottom: 15px;
      @media (max-width: 375px) {
        justify-content: center;
      }

      h3 {
        font-size: 26px;
        margin: 0;
        @media (max-width: 480px) {
          font-size: 20px;
        }

        &.price-new {
          color: $color-dark;
          font-family: "Roboto-Bold";
        }
        &.price-old {
          margin: 0 10px;
          font-family: "Roboto-Regular";
          color: #a4b3b6;
          text-decoration: line-through;
        }
      }
    }
    .counter {
      margin-bottom: 20px;
      @media (max-width: 375px) {
        justify-content: center;
        align-content: center;
        width: 100%;
      }

      h4 {
        font-size: 20px;
        margin-right: 10px;

        body.ar & {
          margin-right: unset;
          margin-left: 10px;
        }
      }
      width: 30%;
      align-items: center;
      @media (max-width: 480px) {
        span {
          margin-bottom: 5px;
        }
      }

      i {
        font-size: 26px;
        @media (max-width: 480px) {
          font-size: 20px;
        }
      }
    }
    .option-box {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      margin-bottom: 10px;
      margin-right: auto;
      width: 100%;
      justify-content: flex-end;
      @media (max-width: 375px) {
        justify-content: center;
      }
      h3 {
        text-transform: capitalize;
        font-size: 20px;
        font-family: "Roboto-Regular";
        margin-right: 10px;
        body.ar & {
          margin-right: unset;
          margin-left: 10px;
        }
      }
      label {
        margin-bottom: 0;
        margin-left: 10px;
        cursor: pointer;
        color: $color-dark;
        body.ar & {
          margin-left: unset;
          margin-right: 10px;
        }
        position: relative;
        margin-bottom: 6px;
        i {
          font-size: 18px;
          transition: 0.3s;
          opacity: 1;
          backface-visibility: hidden;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        i.fa-check-square {
          opacity: 0;
          transition: 0.5s;
        }
      }
      input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        &:checked ~ label i {
          opacity: 0;
        }
        &:checked ~ label i.fa-check-square {
          opacity: 1;
        }
      }
    }
    .buttonsContainer {
      display: flex;
      align-items: center;
      @media (max-width: 375px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      .min-button {
        // width: 30%;
        width: 215px;
        @media (max-width: 768px) {
          font-size: 15px;
        }
        @media (max-width: 768px) {
          height: 40px;
          font-size: 15px;
          width: 170px;
        }
        @media (max-width: 375px) {
          margin-bottom: 10px;
        }
        &.reverse {
          margin-right: auto;
          color: #fff;
          body.ar & {
            margin-right: unset;
            margin-left: auto;
          }
          @media (max-width: 375px) {
            margin-right: unset;
            margin-left: unset;
          }
          i {
            margin-left: 17px;
            font-size: 25px;
            @media (max-width: 768px) {
              font-size: 18px;
            }
          }
          body.ar & {
            margin-right: unset;
            margin-left: auto;
            i {
              margin-left: unset;
              margin-right: 17px;
            }
          }
        }
      }
      .favorite {
        margin-left: 7px;

        body.ar & {
          margin-left: unset;
          margin-right: 7px;
        }
        @media (max-width: 375px) {
          margin-right: unset;
          margin-left: unset;
        }
        width: 42px;
        height: 42px;
        background-color: #919296;
        border-radius: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s;
        @media (max-width: 768px) {
          width: 37px;
          height: 37px;
        }

        i {
          color: #fff;
          font-size: 25px;
          @media (max-width: 768px) {
            font-size: 20px;
          }
        }
        &:hover {
          background-color: $color-dark;
        }
      }
    }
  }
  form {
    margin-top: 30px;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #919294;
    @media (max-width: 480px) {
      padding: 15px;
    }

    h2 {
      font-family: "Roboto-Regular";
      font-size: 30px;
      margin-bottom: 20px;
      @media (max-width: 375px) {
        font-size: 16px;
      }
    }
    .form-container {
      padding: 15px;
      @media (max-width: 480px) {
        padding: 0;
      }
      p {
        color: #919294;
        font-family: "Roboto-Regular";
        font-size: 20px;
        @media (max-width: 375px) {
          font-size: 16px;
        }
      }
      h3 {
        font-size: 20px;
        margin-bottom: 20px;
        @media (max-width: 375px) {
          font-size: 16px;
        }
      }
      .input-container {
        width: 100%;
        input,
        textarea {
          width: 100%;
          margin-bottom: 25px;
          padding: 15px;
          border: 1px solid #919294;
          border-radius: 10px;
          font-size: 16px;
          color: #919296;
          outline: none;
          transition: 0.5s;
          &:focus {
            border-color: #f3a01e;
          }
        }
        textarea {
          height: 162px;
        }
      }
      .rating {
        h4 {
          font-size: 20px;
          @media (max-width: 375px) {
            font-size: 16px;
          }
        }
      }
      .min-button {
        margin-left: auto;
        body.ar & {
          margin-right: auto;
          margin-left: unset;
        }
        @media (max-width: 375px) {
          font-size: 16px;
        }
      }
    }
  }
}
