.customerInfo-section {
  padding: 30px 0;

  .information {
    border: 1px solid #707070;
    border-radius: 10px;
    padding: 20px;
    @media (max-width: 768px) {
      padding: 15px;
    }
    @media (max-width: 375px) {
      padding: 10px;
    }
    .prag {
      font-family: "Roboto-Regular";
      font-size: 16px;
      color: #919294;
      text-transform: capitalize;
    }

    h3 {
      color: #000000;
      text-transform: capitalize;
      font-size: 25px;
      font-family: "Roboto-Medium";
    }
    input {
      padding: 10px;
      border: 1px solid #919294;
      border-radius: 10px;
      font-size: 14px;
      font-family: "Roboto-Regular";
      color: #919294;
    }
    .input-top-container {
      width: 100%;
      padding: 20px 0;
      display: flex;
      //   justify-content: center;
      @media (max-width: 375px){
        flex-direction: column;
        width: 100%
      }

      align-items: center;
      input {
        width: calc(100% / 2.2);
        @media (max-width: 375px){
          width: 100%;
          margin-bottom: 10px
        }

        &:first-of-type {
          margin-right: 20px;
          @media (max-width: 375px){
             margin-right: unset;
          }

          body.ar & {
            margin-right: unset;
            margin-left: 20px;
            @media (max-width: 375px){
              margin-left: unset;
             }

          }
        }
      }

    }
    .radioBox {
      align-items: center;
      display: flex;
      margin-right: 25px;
      @media (max-width: 768px) {
        margin-right: 20px;
      }

      .radioContainer {
        position: relative;
        width: 27px;
        height: 27px;
        @media (max-width: 768px) {
          width: 20px;
          height: 20px;
        }

        label {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 27px;
          height: 27px;
          border-radius: 50%;
          border: 2px solid #f3a01e;
          margin: 0;
          cursor: pointer;
          @media (max-width: 768px) {
            width: 20px;
            height: 20px;
          }

          span {
            opacity: 0;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            width: 103%;
            height: 103%;
            background-color: #f3a01e;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.5s;
            @media (max-width: 768px) {
              margin: 0;
            }

            &::before {
              display: block;
              content: "";
              width: 10px;
              height: 10px;
              background-color: #000;
              border-radius: 50%;
            }
          }
        }
        input {
          opacity: 0;
          position: absolute;
          &:checked ~ label span {
            opacity: 1;
          }
        }
      }
      body.ar & {
        margin-left: 25px;
        margin-right: unset;
      }

      label {
        margin-left: 10px;
        margin-top: 5px;
        text-transform: capitalize;
        font-family: "Roboto-Regular";
        font-size: 20px;
        @media (max-width: 768px) {
          font-size: 15px;
        }

        body.ar & {
          margin-left: unset;
          margin-right: 10px;
        }
      }
    }
    .order-type-container {
      display: flex;
      align-items: center;
      width: 100%;
      padding: 20px 0;
    }
    .selectBox {
      padding: 20px 0;
      select {
        width: 100%;
        padding: 10px;
        border-radius: 10px;
        background-color: #fff;
        font-size: 14px;
        font-family: "Roboto-Regular";
        outline: none;
        cursor: pointer;
        option {
          padding: 60px;
        }
      }
    }
    .inputLgContainer {
      width: 100%;
      padding: 20px 0;
      input {
        width: 100%;
      }
    }
    .inputSmContainer {
      display: flex;
      justify-content: space-between;
      @media (max-width: 375px){
        flex-direction: column;
        width: 100%
      }
      input {
        width: calc(100% / 3.3);
        @media (max-width: 375px){
           width: 100%;
           margin-bottom: 10px
        }

      }
      .box {
        display: flex;
        flex-direction: column;
        width: calc(100% / 3.3);
        @media (max-width: 375px){
          width: 100%
        }

        input {
          width: 100%;
        }
      }
    }
  }
  .shippingCart {
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    background-color: #f9f9f9;
    overflow: hidden;
    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
    h3 {
      padding: 20px;
      background-color: #4298d3;
      box-shadow: 0px 3px 6px #00000029;
      border-radius: 10px 10px 0px 0px;
      color: #ffffff;
      text-transform: capitalize;
      font-family: "Roboto-Medium";
      font-size: 25px;
    }
    .productBox {
      padding: 10px 0;
      display: flex;
      margin-bottom: 10px;
      .imageBox {
        padding: 10px;
        width: 36%;
      }
      .ditelse {
        h5 {
          font-family: "Roboto-Regular";
          font-size: 16px;
        }
        h6 {
          color: #4399d4;
          font-family: "Roboto-Bold";
          text-transform: uppercase;
          font-size: 20px;
        }
      }
      .control {
        p {
          margin-bottom: 0;
        }
        display: flex;
        align-items: center;
      }
    }
    .totals {
      padding: 15px;
      margin-top: 50px;

      .box {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        h5 {
          text-transform: capitalize;
          font-size: 16px;
          font-family: "Roboto-Regular";
        }
        p {
          margin-bottom: 0;
          text-transform: capitalize;
          font-size: 16px;
          font-family: "Roboto-Regular";
        }
      }
    }
  }
  .top-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .imageBox {
      margin-bottom: auto;
    }
    .radioContent {
      p {
        width: 60%;
        @media (max-width: 480px) {
          width: 100%;
        }
        margin-left: 40px;
        body.ar & {
          margin-left: unset;
          margin-right: 40px;
        }
        @media (max-width: 480px) {
          margin-left: unset;
          margin-right: unset;
        }
      }
    }
  }
  .inputLabel {
    text-transform: capitalize;
    font-family: "Roboto-Regular";
    font-size: 20px;
  }
  .min-button {
    margin: auto;
    margin-top: 30px;
    width: max-content;
  }
}
.orderConfirmation-section {
  .title {
    display: flex;
    justify-content: space-between;
    .sections-title {
      margin-bottom: 0;
      @media (max-width: 480px) {
        font-size: 20px;
      }
      @media (max-width: 320px) {
        font-size: 16px;
      }
    }
    .left-box {
      display: flex;
      align-items: center;

      h5 {
        font-family: "Roboto-Regular";
        font-size: 16px;
        margin-bottom: 0;
        margin-right: 20px;
        @media (max-width: 320px) {
          margin-right: 10px;
        }

        body.ar & {
          margin-right: unset;
          margin-left: 20px;
          @media (max-width: 320px) {
            margin-left: 10px;
          }
        }

        span {
          text-transform: uppercase;
          margin-left: 10px;
          @media (max-width: 320px) {
            margin-left: unset;
          }

          body.ar & {
            margin-left: unset;
            margin-right: 10px;
          }
        }
      }
      button {
        padding: 10px 20px;
        background-color: #f3a01e;
        border-radius: 10px;
        border: none;
        padding: 5px 20px;
        font-family: "Roboto-Regular";
        font-size: 25px;
        text-transform: capitalize;
        @media (max-width: 480px) {
          font-size: 15px;
        }

        transition: 0.5s;
        &:hover {
          background-color: #4399d4;
          color: #fff;
        }
      }
    }
  }
  .information-box {
    padding: 30px 0;
    border: 1px solid #707070;
    border-radius: 10px;
    padding: 25px;
    @media (max-width: 480px) {
      padding: 10px;
    }
    @media (max-width: 991px) {
      margin-bottom: 30px
    }

    .boxLg {
      .title-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 17px;
        border-bottom: 2px dotted #919294;
        padding-bottom: 10px;
        h2 {
          font-family: "Roboto-Medium";
          font-size: 23px;
          text-transform: capitalize;
          margin-bottom: 0;
        }
        a {
          background-color: transparent;
          border: transparent;
          font-size: 20px;
          color: #f3a01e;
          transition: 0.5s;
          &:hover {
            color: #4399d4;
          }
        }
      }
      h3 {
        font-family: "Roboto-Medium";
        font-size: 20px;
        text-transform: capitalize;
      }
      p {
        font-family: "Roboto-Medium";
        margin-bottom: 5px;
      }
    }
  }
}
