.cart-section{
  .order-box {
    display: flex;
    @media (max-width: 480px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }

    align-items: center;
    background-color: #f9f9f9;
    border-radius: 10px;
    padding: 10px;
    @media (max-width: 768px) {
      width: 100%;
    }

    margin-bottom: 20px;
    .image-box {
      padding: 15px;
      width: 25%;
      height: 100%;
      flex: 1;
      @media (max-width: 480px) {
        width: 100%;
      }
    }
    position: relative;
    	.control{
        display: flex;
        align-items: center;
        h6{
          margin-bottom:3px;
          span{
            margin-left: 10px;
            margin-right: 10px;
           }
        }
      }
    button.close {
      position: absolute;
      top: 11px;
      right: 11px;
      color: #e91515;
      transition: 0.5s;
    }
    .info {
      width: 100%;
      flex: 3;

      h3 {
        font-size: 30px;
        font-family: "Roboto-Regular";
      }
      h4 {
        color: #4399d4;
        text-transform: uppercase;
        font-size: 25px;
        font-family: "Roboto-Bold";
      }

      button.open-side-cart {
        display: block;
        margin-left: auto;
        body.ar & {
          margin-left: unset;
          margin-right: auto;
        }
        @media (max-width: 480px) {
          margin-left: unset;
          margin-right: unset;
          margin: auto;
        }
        background-color: #f3a01e;
        border-radius: 10px;
        border: none;
        padding: 5px 20px;
        font-family: "Roboto-Regular";
        text-transform: capitalize;
        font-size: 25px;
        transition: 0.5s;
        &:hover {
          background-color: #4399d4;
          color: #fff;
        }
      }
    }
  }
  .total-container{
    width: 100%;
    background-color: #f9f9f9;
    border-radius: 10px;
    padding: 20px 0 0;
    overflow: hidden;
    margin-bottom: 20px;
    h2{
      padding: 10px;
      margin-bottom: 10px;
      font-family: "Roboto-Regular";
      font-size: 20px;
      color: #000000;
      text-transform: capitalize;
      & > span{
        display: block;
        font-family: "Roboto-Bold";
        font-size: 25px;
        span{
          color: #919294;
          text-transform: uppercase;
          font-family: "Roboto-Regular";
          font-size: 20px;
        }
      }
    }
    .min-button{
      border-radius: unset;
      width: 100%;
    }
  }
}
